@include font('HelveticaNeue', 'HelveticaNeue');
@include font('KudryashevDisplay', 'KudryashevDisplay');

@mixin fhelv {
  font-family: 'HelveticaNeue', 'Arial', sans-serif; }

@mixin fkudr {
  font-family: 'KudryashevDisplay','Arial', sans-serif; }

body {
  font-size: 18px;
  @include fhelv;
  color: $text-color; }

h1 {
  @include fkudr;
  font-size: 130px;
  text-transform: uppercase;
  font-weight: 400; }

h2 {
  @include fkudr;
  font-size: 90px;
  text-transform: uppercase;
  font-weight: 400; }

h3 {
  @include fkudr;
  font-size: 70px;
  text-transform: uppercase;
  font-weight: 400; }

h4 {
  @include fkudr;
  font-size: 60px;
  text-transform: uppercase;
  font-weight: 400; }

h5 {
  @include fkudr;
  font-size: 50px;
  text-transform: uppercase;
  font-weight: 400; }

h6 {
  @include fkudr;
  font-size: 40px;
  text-transform: uppercase;
  font-weight: 400; }
