.video {
  max-height: 1140px;

  margin-bottom: 390px;

  &__content {
    max-height: 1000px;
    width: 100%;
    margin-bottom: -10px; }


  &__quote {
    width: 100%;
    background-color: $gold-background-color; }

  &__quote-wrapper {
    position: relative;
    text-align: center;

    max-width: 1034px;
    margin: 0 auto;
    padding: 72px 0; }

  &__quote-text {
    @include fkudr;
    font-size: 40px;
    line-height: 40px;
    color: #000;

    &::after,
    &::before {
      content: url('/img/quotes/quotes.svg');

      height: 20px;
      width: 20px; }

    &::before {
      position: relative;
      bottom: 20px;
      right: 10px; }

    &::after {
      position: relative;
      top: 10px;
      left: 10px; } }

  &__pattern {
    position: absolute;
    bottom: -45px; } }
