* {
  box-sizing: border-box;
}

*::before, *::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  padding: 0;
  font: inherit;
}

@font-face {
  font-family: "HelveticaNeue";
  src: url("../fonts/HelveticaNeue.ttf") format("ttf"), url("../fonts/HelveticaNeue.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "KudryashevDisplay";
  src: url("../fonts/KudryashevDisplay.ttf") format("ttf"), url("../fonts/KudryashevDisplay.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-size: 18px;
  font-family: 'HelveticaNeue', 'Arial', sans-serif;
  color: #fff;
}

h1 {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 130px;
  text-transform: uppercase;
  font-weight: 400;
}

h2 {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 90px;
  text-transform: uppercase;
  font-weight: 400;
}

h3 {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 70px;
  text-transform: uppercase;
  font-weight: 400;
}

h4 {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 60px;
  text-transform: uppercase;
  font-weight: 400;
}

h5 {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 50px;
  text-transform: uppercase;
  font-weight: 400;
}

h6 {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 40px;
  text-transform: uppercase;
  font-weight: 400;
}

body {
  background-color: #181818;
}

.navigation__list {
  display: flex;
}

.navigation__item {
  margin-right: 40px;
  text-transform: uppercase;
  font-size: 14px;
  transition: color 0.3s;
}

.navigation__item:hover {
  color: #9d8151;
}

.navigation__item:last-child {
  margin-right: 0;
}

.incredible-jewerly {
  position: relative;
  height: 1555px;
  margin-bottom: 300px;
}

.incredible-jewerly__inner {
  max-width: 1400px;
  margin: 0 auto;
}

.incredible-jewerly__title {
  margin-bottom: 120px;
  line-height: 117px;
  white-space: break-spaces;
}

.incredible-jewerly__text-wrapper {
  position: relative;
  left: 105px;
  width: 310px;
}

.incredible-jewerly__text {
  font-size: 16px;
  line-height: 22.4px;
  color: #e3e3e3;
}

.incredible-jewerly__main-image {
  position: absolute;
  top: 70px;
  right: 0;
  z-index: -50;
  width: 900px;
  height: 1315px;
  background-image: url("/img/incredible-jewerly/main.jpg");
  background-repeat: no-repeat;
  background-position: 100% 50%;
  transform: rotateY(180deg);
}

.incredible-jewerly__pattern {
  position: absolute;
  bottom: -45px;
  left: 20px;
}

.incredible-jewerly__secondary-image {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 350px;
  height: 345px;
  background-image: url("/img/incredible-jewerly/secondary.png");
  background-size: 100%;
  background-repeat: no-repeat;
}

.incredible-jewerly__secondary-image::after {
  content: '';
  position: absolute;
  z-index: -50;
  width: 424px;
  height: 1px;
  background-color: #9d8151;
  transform: rotate(-45deg);
}

.personally-select {
  position: relative;
  margin-left: auto;
  width: 900px;
  margin-bottom: 350px;
}

.personally-select__title-wrapper {
  width: 565px;
}

.personally-select__title {
  margin-bottom: 70px;
  line-height: 77px;
}

.personally-select__arrow {
  position: absolute;
  bottom: 0;
}

.personally-select__text-wrapper {
  margin-left: auto;
  margin-right: 120px;
  width: 355px;
}

.personally-select__text {
  margin-bottom: 50px;
  font-size: 16px;
  line-height: 22.4px;
  color: #e3e3e3;
}

.personally-select__link {
  font-size: 16px;
  text-transform: uppercase;
  text-decoration: underline;
  color: #9d8151;
}

.most-fabulous {
  display: grid;
  grid-template-columns: 30% 70%;
  grid-template-rows: 860px 370px;
  margin-left: auto;
  margin-bottom: 170px;
