}

.most-fabulous__title-wrapper {
  width: 400px;
}

.most-fabulous__title {
  margin-bottom: 70px;
  line-height: 44px;
}

.most-fabulous__text-wrapper {
  width: 295px;
  margin-bottom: 50px;
}

.most-fabulous__text {
  font-size: 16px;
  line-height: 22.4px;
  color: #e3e3e3;
}

.most-fabulous__link {
