.video {
  max-height: 1140px;
  margin-bottom: 390px;
}

.video__content {
  max-height: 1000px;
  width: 100%;
  margin-bottom: -10px;
}

.video__quote {
  width: 100%;
  background-color: #c6a56e;
}

.video__quote-wrapper {
  position: relative;
  text-align: center;
  max-width: 1034px;
  margin: 0 auto;
  padding: 72px 0;
}

.video__quote-text {
  font-family: 'KudryashevDisplay','Arial', sans-serif;
  font-size: 40px;
  line-height: 40px;
  color: #000;
}

.video__quote-text::after, .video__quote-text::before {
  content: url("/img/quotes/quotes.svg");
  height: 20px;
  width: 20px;
}

.video__quote-text::before {
  position: relative;
  bottom: 20px;
  right: 10px;
}

.video__quote-text::after {
  position: relative;
  top: 10px;
  left: 10px;
}

.video__pattern {
  position: absolute;
  bottom: -45px;
}

